import React from "react"
import { styled } from "@rp/react/theme"
import { IconEntity } from "@rp/react/web/icons"
import { ButtonBase, Typography } from "@rp/react/web/mui/core"

const SuggestionOptionStyled = styled("div", { name: "SuggestionOptionStyled" })(() => ({
  display: "flex",
  alignItems: "center",
  width: "100%",
  justifyContent: "space-between",
  padding: "8px 0",
}))

const labelTextStyle = {
  fontWeight: 400,
  fontSize: 14,
  letterSpacing: 0,
  marginLeft: 12,
}

type HightLightProps = {
  highlight?: boolean
  component?: React.ElementType // Workaround because props/types issues https://github.com/mui/material-ui/issues/15695
}

const HightLightText = styled(Typography, {
  name: "HightLightText",
  shouldForwardProp: props => props !== "highlight",
})<HightLightProps>(({ theme, highlight }) => ({
  color: highlight ? theme.palette.primary.light : "inherit",
}))
const SuggestionLeftStyled = styled("div", { name: "SuggestionLeftStyled" })({
  display: "flex",
  alignItems: "center",
  flexShrink: 1,
  minWidth: 0,
})

const SuggestionTickerTextStyled = styled(Typography, {
  name: "SuggestionTickerTextStyled",
})<{ component?: React.ElementType }>(({ theme }) => ({
  color: theme.palette.primary.light,
  fontSize: 14,
  fontWeight: 400,
  letterSpacing: 0,
  marginLeft: 12,
}))

const SuggestionLabel = styled("div", { name: "SuggestionLabel" })(({ theme }) => ({
  marginLeft: theme.spacing(1.5),
  width: "100%",

  "& div": {
    display: "flex",
    alignItems: "center",
  },

  "& p": {
    color: theme.palette.grey[300],
  },
}))

const SuggestionKeywordStyled = styled(Typography, { name: "SuggestionKeywordStyled" })(
  ({ theme }) => ({
    marginLeft: 36,
    color: theme.palette.grey[300],
    fontSize: 16,
  }),
)

const SuggestionTypeTextStyled = styled("div", { name: "SuggestionTypeTextStyled" })(
  ({ theme }) => ({
    color: theme.palette.grey[300],
    fontSize: 12,
    letterSpacing: 0.25,
    marginLeft: theme.spacing(3),
    display: "flex",
    flexShrink: 0,
  }),
)

const SuggestionIcon = styled(IconEntity)(() => ({
  height: 20,
  width: 20,
}))

type TextFieldProps = {
  open?: boolean
  offset?: number
}

const TextFieldContainer = styled("div", {
  name: "TextFieldContainer",
  shouldForwardProp: prop => prop !== "offset",
})<TextFieldProps>(({ theme, offset = 0 }) => ({
  width: "90%",
  margin: "auto",
  backgroundColor: theme.palette.elevation.dp12,
  left: "5%",
  top: offset,
  "&[open]": {
    transition: "top 300ms",
    position: "absolute",
    zIndex: 10,
    top: "0",
    left: "0",
    width: "100%",
  },
}))

const SubsidiariesToggle = styled(ButtonBase)(({ theme }) => ({
  fontSize: 12,
  fontFamily: theme.typography.fontFamily,
  color: theme.palette.primary.light,
  marginLeft: theme.spacing(1.5),

  [theme.breakpoints.down("md")]: {
    fontSize: 16,
    marginLeft: 4,
  },

  ":hover": {
    textDecoration: "underline",
  },
}))

export {
  SuggestionOptionStyled,
  labelTextStyle,
  HightLightText,
  SuggestionLeftStyled,
  SuggestionTickerTextStyled,
  SuggestionLabel,
  SuggestionKeywordStyled,
  SuggestionTypeTextStyled,
  SuggestionIcon,
  TextFieldContainer,
  SubsidiariesToggle,
}
